//@extend-elements
//original selectors
//.mapboxgl-ctrl-top-left, .mapboxgl-ctrl-top-right, .mapboxgl-ctrl-bottom-left, .mapboxgl-ctrl-bottom-right
%extend_1 {
	position: absolute;
	pointer-events: none;
	z-index: 2;
}


.mapboxgl-map {
	font: 12px/20px $font_0, $font_1, $font_2, $font_3;
	overflow: hidden;
	position: relative;
	&:-webkit-full-screen {
		width: 100%;
		height: 100%;
	}
}
.mapboxgl-missing-css {
	display: none;
}
.mapboxgl-canvas-container {
	&.mapboxgl-interactive {
		cursor: grab;
		&:active {
			cursor: grabbing;
		}
	}
	&.mapboxgl-touch-zoom-rotate {
		touch-action: pan-x pan-y;
		&.mapboxgl-touch-drag-pan {
			touch-action: none;
		}
	}
	&.mapboxgl-touch-drag-pan {
	}
}
.mapboxgl-ctrl-nav-compass {
	cursor: grab;
	&:active {
		cursor: grabbing;
	}
}
.mapboxgl-ctrl-top-left {
	@extend %extend_1;
	top: 0;
	left: 0;
	.mapboxgl-ctrl {
		margin: 10px 0 0 10px;
		float: left;
	}
}
.mapboxgl-ctrl-top-right {
	@extend %extend_1;
	top: 0;
	right: 0;
	.mapboxgl-ctrl {
		margin: 10px 10px 0 0;
		float: right;
	}
}
.mapboxgl-ctrl-bottom-left {
	@extend %extend_1;
	bottom: 0;
	left: 0;
	.mapboxgl-ctrl {
		margin: 0 0 10px 10px;
		float: left;
	}
}
.mapboxgl-ctrl-bottom-right {
	@extend %extend_1;
	right: 0;
	bottom: 0;
	.mapboxgl-ctrl {
		margin: 0 10px 10px 0;
		float: right;
	}
}
.mapboxgl-ctrl {
	clear: both;
	pointer-events: auto;
	> button::-moz-focus-inner {
		border: 0;
		padding: 0;
	}
	&.mapboxgl-ctrl-attrib {
		padding: 0 5px;
		background-color: $white_50;
		margin: 0;
	}
	> button:hover {
		background-color: $black_5;
	}
}
.mapboxgl-ctrl-group {
	border-radius: 4px;
	box-shadow: 0 0 0 2px $black_10;
	overflow: hidden;
	background: $white;
	> button {
		width: 30px;
		height: 30px;
		display: block;
		padding: 0;
		outline: none;
		border: 0;
		box-sizing: border-box;
		background-color: transparent;
		cursor: pointer;
		+ button {
			border-top: 1px solid $color_alto_approx;
		}
	}
}
.mapboxgl-ctrl-icon {
	speak: none;
	padding: 5px;
	&.mapboxgl-ctrl-zoom-out {
		background-image: url($data_uri_0);
	}
	&.mapboxgl-ctrl-zoom-in {
		background-image: url($data_uri_1);
	}
	&.mapboxgl-ctrl-geolocate {
		background-image: url($data_uri_2);
		&:disabled {
			background-image: url($data_uri_3);
		}
		&.mapboxgl-ctrl-geolocate-active {
			background-image: url($data_uri_4);
		}
		&.mapboxgl-ctrl-geolocate-active-error {
			background-image: url($data_uri_5);
		}
		&.mapboxgl-ctrl-geolocate-background {
			background-image: url($data_uri_6);
		}
		&.mapboxgl-ctrl-geolocate-background-error {
			background-image: url($data_uri_7);
		}
		&.mapboxgl-ctrl-geolocate-waiting {
			animation: mapboxgl-spin 2s infinite linear;
		}
	}
	&.mapboxgl-ctrl-fullscreen {
		background-image: url($data_uri_8);
	}
	&.mapboxgl-ctrl-shrink {
		background-image: url($data_uri_9);
	}
	> .mapboxgl-ctrl-compass-arrow {
		speak: none;
	}
	&.mapboxgl-ctrl-compass > .mapboxgl-ctrl-compass-arrow {
		width: 20px;
		height: 20px;
		margin: 5px;
		background-image: url($data_uri_10);
		background-repeat: no-repeat;
		display: inline-block;
	}
}
@-webkit-keyframes mapboxgl-spin {
	0% {
	}
	100% {
	}
}
@-moz-keyframes mapboxgl-spin {
	0% {
	}
	100% {
	}
}
@-o-keyframes mapboxgl-spin {
	0% {
	}
	100% {
	}
}
@-ms-keyframes mapboxgl-spin {
	0% {
	}
	100% {
	}
}
@keyframes mapboxgl-spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
.mapboxgl-ctrl-attrib {
	&.mapboxgl-compact {
		padding-top: 2px;
		padding-bottom: 2px;
		margin: 0 10px 10px;
		position: relative;
		padding-right: 24px;
		background-color: $white;
		border-radius: 3px 12px 12px 3px;
		visibility: hidden;
		&:hover {
			visibility: visible;
		}
	}
	&.mapboxgl-compact::after {
		content: '';
		cursor: pointer;
		position: absolute;
		bottom: 0;
		right: 0;
		background-image: $url_1;
		background-color: $white_50;
		width: 24px;
		height: 24px;
		box-sizing: border-box;
		visibility: visible;
		border-radius: 12px;
	}
	a {
		color: $black_75;
		text-decoration: none;
		&:hover {
			color: inherit;
			text-decoration: underline;
		}
	}
	.mapbox-improve-map {
		font-weight: bold;
		margin-left: 2px;
	}
}
.mapboxgl-attrib-empty {
	display: none;
}
.mapboxgl-ctrl-scale {
	background-color: $white_75;
	font-size: 10px;
	border-width: medium 2px 2px;
	border-style: none solid solid;
	border-color: $color_mine_shaft_approx;
	padding: 0 5px;
	color: $color_mine_shaft_approx;
	box-sizing: border-box;
}
.mapboxgl-popup {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	will-change: transform;
	pointer-events: none;
}
.mapboxgl-popup-anchor-top {
	flex-direction: column;
	.mapboxgl-popup-tip {
		align-self: center;
		border-top: none;
		border-bottom-color: $white;
	}
}
.mapboxgl-popup-anchor-top-left {
	flex-direction: column;
	.mapboxgl-popup-tip {
		align-self: flex-start;
		border-top: none;
		border-left: none;
		border-bottom-color: $white;
	}
	.mapboxgl-popup-content {
		border-top-left-radius: 0;
	}
}
.mapboxgl-popup-anchor-top-right {
	flex-direction: column;
	.mapboxgl-popup-tip {
		align-self: flex-end;
		border-top: none;
		border-right: none;
		border-bottom-color: $white;
	}
	.mapboxgl-popup-content {
		border-top-right-radius: 0;
	}
}
.mapboxgl-popup-anchor-bottom {
	flex-direction: column-reverse;
	.mapboxgl-popup-tip {
		align-self: center;
		border-bottom: none;
		border-top-color: $white;
	}
}
.mapboxgl-popup-anchor-bottom-left {
	flex-direction: column-reverse;
	.mapboxgl-popup-tip {
		align-self: flex-start;
		border-bottom: none;
		border-left: none;
		border-top-color: $white;
	}
	.mapboxgl-popup-content {
		border-bottom-left-radius: 0;
	}
}
.mapboxgl-popup-anchor-bottom-right {
	flex-direction: column-reverse;
	.mapboxgl-popup-tip {
		align-self: flex-end;
		border-bottom: none;
		border-right: none;
		border-top-color: $white;
	}
	.mapboxgl-popup-content {
		border-bottom-right-radius: 0;
	}
}
.mapboxgl-popup-anchor-left {
	flex-direction: row;
	.mapboxgl-popup-tip {
		align-self: center;
		border-left: none;
		border-right-color: $white;
	}
}
.mapboxgl-popup-anchor-right {
	flex-direction: row-reverse;
	.mapboxgl-popup-tip {
		align-self: center;
		border-right: none;
		border-left-color: $white;
	}
}
.mapboxgl-popup-tip {
	width: 0;
	height: 0;
	border: 10px solid transparent;
	z-index: 1;
}
.mapboxgl-popup-close-button {
	position: absolute;
	right: 0;
	top: 0;
	border: 0;
	border-radius: 0 3px 0 0;
	cursor: pointer;
	background-color: transparent;
	&:hover {
		background-color: $black_5;
	}
}
.mapboxgl-popup-content {
	position: relative;
	background: $white;
	border-radius: 3px;
	box-shadow: 0 1px 2px $black_10;
	padding: 10px 10px 15px;
	pointer-events: auto;
}
.mapboxgl-marker {
	position: absolute;
	top: 0;
	left: 0;
	will-change: transform;
}
.mapboxgl-user-location-dot {
	background-color: $color_dodger_blue_approx;
	width: 15px;
	height: 15px;
	//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
	border-radius: 50%;
	//Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
	box-shadow: 0 0 2px $black_25;
}
.mapboxgl-user-location-dot::before {
	background-color: $color_dodger_blue_approx;
	content: '';
	width: 15px;
	height: 15px;
	//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
	border-radius: 50%;
	position: absolute;
	animation: mapboxgl-user-location-dot-pulse 2s infinite;
}
.mapboxgl-user-location-dot::after {
	//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
	border-radius: 50%;
	border: 2px solid $white;
	content: '';
	height: 19px;
	left: -2px;
	position: absolute;
	top: -2px;
	width: 19px;
	//Instead of the line below you could use @include box-sizing($bs)
	box-sizing: border-box;
}
@-webkit-keyframes mapboxgl-user-location-dot-pulse {
	0% {
		opacity: 1;
	}
	70% {
		opacity: 0;
	}
	100% {
		opacity: 0;
	}
}
@-ms-keyframes mapboxgl-user-location-dot-pulse {
	0% {
		opacity: 1;
	}
	70% {
		opacity: 0;
	}
	100% {
		opacity: 0;
	}
}
@keyframes mapboxgl-user-location-dot-pulse {
	0% {
		transform: scale(1);
		opacity: 1;
	}
	70% {
		transform: scale(3);
		opacity: 0;
	}
	100% {
		transform: scale(1);
		opacity: 0;
	}
}
.mapboxgl-user-location-dot-stale {
	background-color: $color_silver_chalice_approx;
}
.mapboxgl-user-location-dot-stale::after {
	display: none;
}
.mapboxgl-crosshair {
	cursor: crosshair;
	.mapboxgl-interactive {
		cursor: crosshair;
		&:active {
			cursor: crosshair;
		}
	}
}
.mapboxgl-boxzoom {
	position: absolute;
	top: 0;
	left: 0;
	width: 0;
	height: 0;
	background: $white;
	border: 2px dotted $color_log_cabin_approx;
	opacity: 0.5;
}
@media print {
	.mapbox-improve-map {
		display: none;
	}
}
a.mapboxgl-ctrl-logo {
	width: 85px;
	height: 21px;
	margin: 0 0 -3px -3px;
	display: block;
	background-repeat: no-repeat;
	cursor: pointer;
	background-image: $url_0;
}